<template>
  <div id="app">
    <img :src="imgUrl" @click="changUrl">
    <router-view/>
  </div>
</template>

<script>
export default {
	name: 'App',
	data() {
		return {
			imgUrl: '../static/logo.png',
		};
	},
	methods: {
		changUrl() {
			this.imgUrl += '1';
			let a = 1;
			a[0] = 1;
		},
	},
};
</script>

<style>
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	margin-top: 60px;
}
</style>



